.slider {
    display: flex;
    padding-top: 2%;
    overflow: hidden;
    position: relative;
    max-height: 500px;
    width: 100%;
    margin: 20px auto;
}

.slide {
    position: relative;
    min-width: 100%;
    max-height: 100%;
    transition: 0.5s;
    padding: 0 1px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.slide img {
    max-width: 100%;
}

.subtitle {
    background-color: white;
    position: relative;
    width: 100%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.42);
    transform: translateY(-50%);
}

.seta {
    position: absolute;
    width: 10%;
    height: 100%;
    background: none;
    transition: 0.5s;
    border: none;
    outline: none;
    color: black;
    display: flex;
    align-items: center;
    font-size: 20px;
}

.goLeft {
    left: 0;
    justify-content: flex-start;
}


.goRight {
    right: 0;
    justify-content: flex-end;
}